  font-family: "Merriweather", "Times New Roman", Times, serif;
  color: white;
}

html,
body {
  margin: 0;
  padding: 0;
}

body {
  background: url(${___CSS_LOADER_URL_REPLACEMENT_0___}) no-repeat center center fixed;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
}

.loader {
  width: 48px;
  height: 48px;
  border: 5px solid #fff;
  border-bottom-color: #0f766e;
  border-radius: 50%;
  display: inline-block;
  box-sizing: border-box;
  animation: rotation 1s linear infinite;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

#main-content {
  height: 100vh;
  display: grid;
  grid-template: auto 2fr / 1fr;
  align-items: flex-start;
}

.nav-bar {
  max-height: 10%;
  display: grid;
  grid-template: 1fr/2fr 1fr;
  align-items: center;
  padding: 3em;
  gap: 1em;
  background-color: #0284c780;
  border-radius: 0 0 1em 1em;
}

.nav-bar .search-bar {
  justify-self: flex-start;

  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1em;
}

#city-name-search {
  appearance: none;
  background-color: #082f49;
  border-color: transparent;
  border-radius: 1em;
  padding: 0.5em 0.2em;
  width: 300px;
}

.nav-bar .search-bar .search-icon {
  width: 24px;
  height: 24px;
  content: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
}

.nav-bar .search-bar .search-icon:hover {
  transition: all 0.1s ease;
  transform: scale(1.05);
}

.nav-bar .search-location {
  justify-self: flex-end;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  width: 100%;
  text-align: end;
}

.content {
  align-self: center;
  justify-self: center;

  display: none;
  grid-template: auto 1fr / auto;
  align-items: flex-start;
  gap: 1em;
  padding: 1em;
  min-width: 70%;
  max-width: 1000px;
}

.content .current-day-content,
.content .forecast-content .forecast-card {
  padding: 1em;
  display: flex;
  gap: 1em;
  background-color: rgba(22, 52, 65, 0.5);
  border-radius: 1em;
  justify-content: space-around;
  align-items: center;
}

.content .forecast-content {
  display: grid;
  gap: 1em;
  grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
}

.content .current-day-content {
  font-size: large;
  max-height: 400px;
  .left-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    .weather-icon {
      width: 128px;
      height: 128px;
    }
  }
  .right-content {
    display: flex;
    flex-direction: column;
    gap: 1em;
    justify-content: center;
    align-items: center;
  }
}
.content .forecast-content .forecast-card {
  font-size: medium;

  .left-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    .weather-icon {
      width: 64px;
      height: 64px;
    }
  }

  .right-content {
    display: flex;
    flex-direction: column;
    gap: 1em;
    justify-content: center;
    align-items: center;
    .graph-container {
      background-color: #082f49;
      padding: 1em;
      border-radius: 1em;
    }
  }
}

.loading-page-container {
  align-self: center;
  justify-self: center;
  display: none;
}

.error-page-container {
  align-self: center;
  justify-self: center;
  display: none;
}

.error-page-container .error-text {
  font-size: large;
  font-weight: 700;
}
